import React, { Component } from 'react';

// this
// 1. this.change = this.change.bind(this)
// 2. change = ()=>{}
// 3. onChange={()=>this.change()}

// reactvalue onChange

export default class Search extends Component {
    constructor (props) {
        super(props)
        this.state = {
            name: ""
        };
        // this.change = this.change.bind(this);
    }

    btn = () => {
        //this
        console.log("btn");
    };

    change = e => {
        // console.log(e);
        let value = e.target.value;
        this.setState({
            name: value,
        });
        console.log("name", this.state.name);
    };

    render () {
        const { name } = this.state;
        return (
            <div>
                <button onClick={this.btn}></button>
                <input type="text" name={name} onChange={this.change}/>
            </div>
        )
    }
}